/*_app.min.css*/
.App{text-align:center}.Jumbo{background:Url(./imgs/yo.png) noRepeat center center fixed;background-size:cover;overflow:hidden}.App-logo{animation:App-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}.fixed-top{display:fixed inline}.events{list-style:none;margin:0;padding:0}.events .ant-badge-status{overflow:hidden;white-space:nowrap;width:100%;text-overflow:ellipsis;font-size:12px}.notes-month{text-align:center;font-size:28px}.notes-month section{font-size:28px}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}#components-layout-demo-side .logo{height:32px;background:rgba(255,255,255,.2);margin:16px}
